// *** begin view *** //
.view {
   position: relative;
   padding: 120px 0 80px;
   background: $color9 url("../img/bg/gray_dots.png") no-repeat 50% top;
   @include md {
      padding: 70px 0; }
   @include sm {
      padding: 20px 0; }
   &::after {
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      height: 230px;
      z-index: 1;
      background: url("../img/decor/gray-tail-bot.png") 50% 100%;
      content: ''; }

   &__decor {
      @include md {
         display: none; }
      &_1 {
         z-index: 2;
         top: 143px;
         right: 66px;
         width: percentage(183 / 1920);
         padding-top: percentage(205 / 1920);
         @include lg {
            right: -9px; } }
      &_2 {
         top: 480px;
         right: 100px;
         width: percentage(92 / 1920);
         padding-top: percentage(69 / 1920);
         @include lg {
            right: 29px; } }
      &_cross-1 {
         bottom: 110px;
         left: 53px;
         width: 138px;
         height: 131px;
         fill: #e6e6e6; }
      &_cross-2 {
         top: 85px;
         right: 20px;
         width: 159px;
         height: 151px;
         fill: #e6e6e6; } }

   &__row {
      position: relative; }
   &__block {
      &-img-wrap {
         @include ratio(255, 255); } }
   &__more {
      height: 68px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      @include sm {
         height: 55px; }
      &-link {
         font-size: 18px; } }

   &__divider {
      position: absolute;
      left: 0;
      width: 100%;
      height: 143px;
      pointer-events: none;
      transition: 1.5s ease-in-out;
      @include md {
         display: none; }
      &_1 {
         top: 196px; }
      &_2 {
         top: 480px;
         opacity: 0;
         .is-active & {
            opacity: 1; } }
      &_3 {
         top: 768px;
         opacity: 0;
         .is-active & {
            opacity: 1; } }
      &_4 {
         top: 1051px;
         opacity: 0;
         .is-active & {
            opacity: 1; } }

      &::before, &::after {
         position: absolute;
         content: '';
         width: 143px;
         height: 143px;
         background: url("../img/decor/cross-divider.png");
         @include cover; }
      &::before {
         left: calc(25% - 73px); }
      &::after {
         left: calc(75% - 73px); } } }
// end view //
