// *** begin section-title *** //
.section-title {
   $parent: &;
   text-align: center;
   @include sm;
   &__text {
      position: relative;
      display: inline-flex;
      align-items: center;
      @include f('int', 36, 28);
      line-height: 1.2;
      color: $black;
      @include sm {
         font-size: 20px; }

      #{$parent}_simple & {
         font-size: 42px;
         line-height: 42px;
         @include sm {
            font-size: 26px;
            line-height: 30px; } }

      &::before, &::after {
         display: inline-flex;
         content: '';
         @include sm {
            transform: scale(.75); }
         #{$parent}_simple & {
            display: none; } }

      &::before {
         @include sm {
            margin-right: -5px; } }
      &::after {
         @include sm {
            margin-left: -5px; } }
      span {
         padding: 20px 22px;
         #{$parent}_simple & {
            padding: 0; } }

      #{$parent}_white & {
         span {
            background-color: $white; }
         &::before {
            @include sprite('sect-title_white-l'); }
         &::after {
            @include sprite('sect-title_white-r'); } }

      #{$parent}_yellow & {
         span {
            background-color: $color1; }
         &::before {
            @include sprite('sect-title_yellow-l'); }
         &::after {
            @include sprite('sect-title_yellow-r'); } } } }
// end section-title //

// *** begin intro-title *** //
.intro-title {
   @include f('int', 36);
   line-height: 1.2;
   color: $color1;
   @include sm {
      font-size: 26px; }
   span {
      color: $white; } }
// end intro-title //

// *** begin sub-title *** //
.sub-title {
   position: relative;
   display: inline-block;
   padding: 7px 52px;
   text-align: center;
   background-color: $black;
   @include f('pn400');
   font-size: 30px;
   line-height: 42px;
   color: $white;
   text-transform: uppercase;
   @include sm {
      padding: 7px 10px; }
   &::after {
      @include vcenter;
      z-index: -1;
      width: 589px;
      height: 201px;
      background-image: url("../img/decor/yellow-title-back.png");
      content: '';
      @include sm {
         display: none; } }
   span {
      color: $color1; } }
// end sub-title //

// *** begin section-divider *** //
.section-divider {
   @include ratio(1920, 296);
   @include cover;
   max-height: 350px;
   &_1 {
      background-image: url("../img/bg/divider_1.png"); }
   &_2 {
      background-image: url("../img/bg/divider_2.png"); } }
// end section-divider //

// *** begin video-block *** //
.video-block {
   $parent: &;
   position: relative;
   overflow: hidden;
   @include gpu;

   &__poster {
      @include coverdiv;
      width: 100%;
      height: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;';
      z-index: 11;
      transition: .5s ease-in-out;
      #{$parent}.is-active & {
         opacity: 0;
         pointer-events: none; } }

   video {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'; }
   &__play-btn {
      position: absolute;
      top: calc(50% - 62px);
      left: calc(50% - 64px);
      z-index: 12;
      width: 128px;
      height: 124px;
      pointer-events: auto;
      transition: .5s ease-in-out;
      @include gpu;
      @include sm {
         transform: scale(.5); }
      #{$parent}.is-active & {
         opacity: 0;
         transform: scale(.5);
         pointer-events: none; }
      &::after {
         @include coverdiv;
         border-radius: 100px 60px/100px 60px;
         background-color: $color2;
         transition: .4s ease-in-out;
         content: ''; }
      &:hover {
         &::after {
            border-radius: 60px 100px/60px 100px; }
         svg:nth-child(1) {
            transform: translate3d(0, 0, 0); } }
      svg {
         position: absolute;
         top: calc(50% - 20px);
         left: calc(50% - 17px);
         z-index: 1;
         font-size: 40px;
         @include gpu;
         &:nth-child(1) {
            transform: translate3d(5px, 5px, 0);
            fill: $white;
            transition: .4s ease-in-out; }
         &:nth-child(2) {
            fill: #1e0f00; } } } }

video::-webkit-media-controls-play-button,
video::-webkit-media-controls-overlay-play-button {
   display: none!important; }
// end video-block //

// *** begin socials *** //
.socials {
   display: flex;
   align-items: center;
   a {
      display: flex;
      align-items: center;
      &:not(:last-child) {
         margin-right: 21px; }
      &:hover {
         svg {
            fill: $color1; } }
      svg {
         fill: #83c2d5;
         transition: .3s ease-in-out;
         font-size: 26px; }
      .icon-vk {
         font-size: 28px; } } }
// end socials //
