// *** begin kinds *** //
.kinds {
   position: relative;
   padding: 138px 0 95px;
   @include md {
      padding: 105px 0 95px; }
   @include sm {
      padding: 60px 0 25px; }
   &__decor {
      @include sm {
         display: none; }
      &_1 {
         top: 49%;
         left: 4%;
         width: percentage(220 / 1920);
         padding-top: percentage(291 / 1920);
         @include lg {
            left: 0; }
         @include md {
            left: 3%;
            top: 7%; } }
      &_2 {
         top: 30.5%;
         right: 7.5%;
         width: percentage(202 / 1920);
         padding-top: percentage(222 / 1920);
         @include lg {
            right: -0.5%; }
         @include md {
            right: 3.5%;
            top: 5.5%; } }

      &_3 {
         top: 84.7%;
         right: 3.7%;
         width: percentage(253 / 1920);
         padding-top: percentage(272 / 1920);
         @include rmin(2000) {
            top: 74.7%; }
         @include lg {
            top: 89.7%;
            right: -0.3%; }
         @include md {
            top: 89.2%; } } }
   &__title {
      margin-bottom: 70px;
      @include md {
         margin-bottom: 40px; }
      @include sm {
         margin-bottom: 20px; } }
   &__row {
      position: relative; }

   &__block {
      &-title {
         position: relative;
         padding: 21px 20px 14px;
         overflow: hidden;
         @include md {
            font-size: 16px; }
         @include sm {
            padding: 8px 10px 1px;
            font-size: 14px; }
         &::after {
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100%;
            height: 95px;
            background: url("../img/bg/gray-top.png") 50% 0;
            content: ''; }
         @include cover; }
      &-img-wrap {
         @include ratio(350, 252);
         box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); } }

   &__divider {
      position: absolute;
      left: 0;
      width: 100%;
      height: 143px;
      pointer-events: none;
      @include sm {
         display: none; }
      &_1 {
         top: calc(25% - 80px); }
      &_2 {
         top: calc(50% - 73px); }
      &_3 {
         top: calc(75% - 67px); }
      &::before, &::after {
         position: absolute;
         content: '';
         width: 143px;
         height: 143px;
         background: url("../img/decor/cross-divider.png");
         @include cover; }
      &::before {
         left: calc(33.33333% - 73px); }
      &::after {
         left: calc(66.66666% - 73px); } } }
// end kinds //
