.footer {
   position: relative;
   z-index: 1;
   padding: 30px 0;
   background-color: $black;
   @include rmin(2000) {
      padding: 30px 0 70px; }
   &::before {
      position: absolute;
      top: -67%;
      left: 0;
      width: 100%;
      padding-top: percentage(239 / 1920);
      background-image: url("../img/decor/page-form-paint.png");
      @include cover;
      pointer-events: none;
      content: '';
      @include lg {
         top: -48%; }
      @include md {
         top: -30%; }
      @include sm {
         top: -15%; } }

   &__container {
      display: flex;
      justify-content: space-between;
      @include xs {
         flex-direction: column;
         align-items: center; } }
   &__col {
      display: flex;
      align-items: center; }
   &__logo {
      margin-right: 20px;
      @include sm {
         margin: 0 0 20px; }
      &-img {
         width: 106px;
         height: 106px;
         @include xs {
            width: 70px;
            height: 70px; } } }
   &__copyright {
      color: #b5b5b5;
      text-transform: uppercase;
      font-size: 14px;
      line-height: 36px; } }
