// *** begin request-block *** //
.request-block {
   $parent: &;
   box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
   background-color: $white;
   &__heading {
      display: flex;
      align-items: center;
      padding: 30px;
      min-height: 206px;
      background: url("../img/bg/black_bg-sm.png");
      @include cover;
      text-align: center;
      @include sm {
         min-height: 150px;
         padding: 20px 15px; }
      #{$parent}_popup & {
         flex-direction: column;
         min-height: 195px;
         padding: 35px 30px 30px;
         @include sm {
            padding: 56px 15px 30px; } } }

   &__title {
      @include sm {
         max-width: 260px;
         margin: 0 auto; } }

   &__sub-title {
      margin-top: 15px;
      @include f('int', 18);
      line-height: 1.2;
      color: $white; }

   &__form {
      padding: 22px 41px 50px 47px;
      @include md {
         padding: 20px 25px 35px 25px; }
      @include sm {
         padding: 15px 15px 25px; }
      #{$parent}_popup & {
         padding: 35px 41px 50px 47px;
         @include sm {
            padding: 25px 15px 30px 15px; } }
      &-descr {
         margin-bottom: 27px;
         font-size: 18px;
         @include sm {
            margin-bottom: 18px;
            font-size: 16px; } }
      &-block {}

      &-field {
         &:not(:last-child) {
            margin-bottom: 20px; } }
      &-btn {
         width: 100%; } } }

// end request-block //
